const state = {
  isMask: false,
  isFixed: false,
  switchPlan: false,
  keyBoardStatus: false

}

const mutations = {
  isShowMask (state, payload) {
    state.isMask = payload.isMask
  },
  isFixedApp (state, payload) {
    state.isFixed = payload.isFixed
  },
  switchPlan (state, payload) {
    state.switchPlan = payload.switchPlan
  },
  hideBottomBar (state, payload) {
    state.keyBoardStatus = payload.keyBoardStatus
  }
}

export default {
  state,
  mutations
}



